// Gradients

#gradient {

  // Horizontal gradient, from left to right
  //
  // Creates two color stops, start and end, by specifying a color and position for each color stop.
  // Color stops are not available in IE9 and below.
  .horizontal(sstart-color: #555; send-color: #333; sstart-percent: 0%; send-percent: 100%) {
    background-image: -webkit-linear-gradient(left, sstart-color sstart-percent, send-color send-percent); // Safari 5.1-6, Chrome 10+
    background-image: -o-linear-gradient(left, sstart-color sstart-percent, send-color send-percent); // Opera 12
    background-image: linear-gradient(to right, sstart-color sstart-percent, send-color send-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+
    background-repeat: repeat-x;
    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=1)",argb(sstart-color),argb(send-color))); // IE9 and down
  }

  // Vertical gradient, from top to bottom
  //
  // Creates two color stops, start and end, by specifying a color and position for each color stop.
  // Color stops are not available in IE9 and below.
  .vertical(sstart-color: #555; send-color: #333; sstart-percent: 0%; send-percent: 100%) {
    background-image: -webkit-linear-gradient(top, sstart-color sstart-percent, send-color send-percent);  // Safari 5.1-6, Chrome 10+
    background-image: -o-linear-gradient(top, sstart-color sstart-percent, send-color send-percent);  // Opera 12
    background-image: linear-gradient(to bottom, sstart-color sstart-percent, send-color send-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+
    background-repeat: repeat-x;
    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)",argb(sstart-color),argb(send-color))); // IE9 and down
  }

  .directional(sstart-color: #555; send-color: #333; sdeg: 45deg) {
    background-repeat: repeat-x;
    background-image: -webkit-linear-gradient(sdeg, sstart-color, send-color); // Safari 5.1-6, Chrome 10+
    background-image: -o-linear-gradient(sdeg, sstart-color, send-color); // Opera 12
    background-image: linear-gradient(sdeg, sstart-color, send-color); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+
  }
  .horizontal-three-colors(sstart-color: #00b3ee; smid-color: #7a43b6; scolor-stop: 50%; send-color: #c3325f) {
    background-image: -webkit-linear-gradient(left, sstart-color, smid-color scolor-stop, send-color);
    background-image: -o-linear-gradient(left, sstart-color, smid-color scolor-stop, send-color);
    background-image: linear-gradient(to right, sstart-color, smid-color scolor-stop, send-color);
    background-repeat: no-repeat;
    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=1)",argb(sstart-color),argb(send-color))); // IE9 and down, gets no color-stop at all for proper fallback
  }
  .vertical-three-colors(sstart-color: #00b3ee; smid-color: #7a43b6; scolor-stop: 50%; send-color: #c3325f) {
    background-image: -webkit-linear-gradient(sstart-color, smid-color scolor-stop, send-color);
    background-image: -o-linear-gradient(sstart-color, smid-color scolor-stop, send-color);
    background-image: linear-gradient(sstart-color, smid-color scolor-stop, send-color);
    background-repeat: no-repeat;
    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)",argb(sstart-color),argb(send-color))); // IE9 and down, gets no color-stop at all for proper fallback
  }
  .radial(sinner-color: #555; souter-color: #333) {
    background-image: -webkit-radial-gradient(circle, sinner-color, souter-color);
    background-image: radial-gradient(circle, sinner-color, souter-color);
    background-repeat: no-repeat;
  }
  .striped(scolor: rgba(255,255,255,.15); sangle: 45deg) {
    background-image: -webkit-linear-gradient(sangle, scolor 25%, transparent 25%, transparent 50%, scolor 50%, scolor 75%, transparent 75%, transparent);
    background-image: -o-linear-gradient(sangle, scolor 25%, transparent 25%, transparent 50%, scolor 50%, scolor 75%, transparent 75%, transparent);
    background-image: linear-gradient(sangle, scolor 25%, transparent 25%, transparent 50%, scolor 50%, scolor 75%, transparent 75%, transparent);
  }
}
