// Vendor Prefixes
//
// All vendor mixins are deprecated as of v3.2.0 due to the introduction of
// Autoprefixer in our Gruntfile. They have been removed in v4.

// - Animations
// - Backface visibility
// - Box shadow
// - Box sizing
// - Content columns
// - Hyphens
// - Placeholder text
// - Transformations
// - Transitions
// - User Select


// Animations
.animation(sanimation) {
  -webkit-animation: sanimation;
       -o-animation: sanimation;
          animation: sanimation;
}
.animation-name(sname) {
  -webkit-animation-name: sname;
          animation-name: sname;
}
.animation-duration(sduration) {
  -webkit-animation-duration: sduration;
          animation-duration: sduration;
}
.animation-timing-function(stiming-function) {
  -webkit-animation-timing-function: stiming-function;
          animation-timing-function: stiming-function;
}
.animation-delay(sdelay) {
  -webkit-animation-delay: sdelay;
          animation-delay: sdelay;
}
.animation-iteration-count(siteration-count) {
  -webkit-animation-iteration-count: siteration-count;
          animation-iteration-count: siteration-count;
}
.animation-direction(sdirection) {
  -webkit-animation-direction: sdirection;
          animation-direction: sdirection;
}
.animation-fill-mode(sfill-mode) {
  -webkit-animation-fill-mode: sfill-mode;
          animation-fill-mode: sfill-mode;
}

// Backface visibility
// Prevent browsers from flickering when using CSS 3D transforms.
// Default value is `visible`, but can be changed to `hidden`

.backface-visibility(svisibility) {
  -webkit-backface-visibility: svisibility;
     -moz-backface-visibility: svisibility;
          backface-visibility: svisibility;
}

// Drop shadows
//
// Note: Deprecated `.box-shadow()` as of v3.1.0 since all of Bootstrap's
// supported browsers that have box shadow capabilities now support it.

.box-shadow(sshadow) {
  -webkit-box-shadow: sshadow; // iOS <4.3 & Android <4.1
          box-shadow: sshadow;
}

// Box sizing
.box-sizing(sboxmodel) {
  -webkit-box-sizing: sboxmodel;
     -moz-box-sizing: sboxmodel;
          box-sizing: sboxmodel;
}

// CSS3 Content Columns
.content-columns(scolumn-count; scolumn-gap: sgrid-gutter-width) {
  -webkit-column-count: scolumn-count;
     -moz-column-count: scolumn-count;
          column-count: scolumn-count;
  -webkit-column-gap: scolumn-gap;
     -moz-column-gap: scolumn-gap;
          column-gap: scolumn-gap;
}

// Optional hyphenation
.hyphens(smode: auto) {
  word-wrap: break-word;
  -webkit-hyphens: smode;
     -moz-hyphens: smode;
      -ms-hyphens: smode; // IE10+
       -o-hyphens: smode;
          hyphens: smode;
}

// Placeholder text
.placeholder(scolor: sinput-color-placeholder) {
  // Firefox
  &::-moz-placeholder {
    color: scolor;
    opacity: 1; // Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526
  }
  &:-ms-input-placeholder { color: scolor; } // Internet Explorer 10+
  &::-webkit-input-placeholder  { color: scolor; } // Safari and Chrome
}

// Transformations
.scale(sratio) {
  -webkit-transform: scale(sratio);
      -ms-transform: scale(sratio); // IE9 only
       -o-transform: scale(sratio);
          transform: scale(sratio);
}
.scale(sratioX; sratioY) {
  -webkit-transform: scale(sratioX, sratioY);
      -ms-transform: scale(sratioX, sratioY); // IE9 only
       -o-transform: scale(sratioX, sratioY);
          transform: scale(sratioX, sratioY);
}
.scaleX(sratio) {
  -webkit-transform: scaleX(sratio);
      -ms-transform: scaleX(sratio); // IE9 only
       -o-transform: scaleX(sratio);
          transform: scaleX(sratio);
}
.scaleY(sratio) {
  -webkit-transform: scaleY(sratio);
      -ms-transform: scaleY(sratio); // IE9 only
       -o-transform: scaleY(sratio);
          transform: scaleY(sratio);
}
.skew(sx; sy) {
  -webkit-transform: skewX(sx) skewY(sy);
      -ms-transform: skewX(sx) skewY(sy); // See https://github.com/twbs/bootstrap/issues/4885; IE9+
       -o-transform: skewX(sx) skewY(sy);
          transform: skewX(sx) skewY(sy);
}
.translate(sx; sy) {
  -webkit-transform: translate(sx, sy);
      -ms-transform: translate(sx, sy); // IE9 only
       -o-transform: translate(sx, sy);
          transform: translate(sx, sy);
}
.translate3d(sx; sy; sz) {
  -webkit-transform: translate3d(sx, sy, sz);
          transform: translate3d(sx, sy, sz);
}
.rotate(sdegrees) {
  -webkit-transform: rotate(sdegrees);
      -ms-transform: rotate(sdegrees); // IE9 only
       -o-transform: rotate(sdegrees);
          transform: rotate(sdegrees);
}
.rotateX(sdegrees) {
  -webkit-transform: rotateX(sdegrees);
      -ms-transform: rotateX(sdegrees); // IE9 only
       -o-transform: rotateX(sdegrees);
          transform: rotateX(sdegrees);
}
.rotateY(sdegrees) {
  -webkit-transform: rotateY(sdegrees);
      -ms-transform: rotateY(sdegrees); // IE9 only
       -o-transform: rotateY(sdegrees);
          transform: rotateY(sdegrees);
}
.perspective(sperspective) {
  -webkit-perspective: sperspective;
     -moz-perspective: sperspective;
          perspective: sperspective;
}
.perspective-origin(sperspective) {
  -webkit-perspective-origin: sperspective;
     -moz-perspective-origin: sperspective;
          perspective-origin: sperspective;
}
.transform-origin(sorigin) {
  -webkit-transform-origin: sorigin;
     -moz-transform-origin: sorigin;
      -ms-transform-origin: sorigin; // IE9 only
          transform-origin: sorigin;
}


// Transitions

.transition(stransition) {
  -webkit-transition: stransition;
       -o-transition: stransition;
          transition: stransition;
}
.transition-property(stransition-property) {
  -webkit-transition-property: stransition-property;
          transition-property: stransition-property;
}
.transition-delay(stransition-delay) {
  -webkit-transition-delay: stransition-delay;
          transition-delay: stransition-delay;
}
.transition-duration(stransition-duration) {
  -webkit-transition-duration: stransition-duration;
          transition-duration: stransition-duration;
}
.transition-timing-function(stiming-function) {
  -webkit-transition-timing-function: stiming-function;
          transition-timing-function: stiming-function;
}
.transition-transform(stransition) {
  -webkit-transition: -webkit-transform stransition;
     -moz-transition: -moz-transform stransition;
       -o-transition: -o-transform stransition;
          transition: transform stransition;
}


// User select
// For selecting text on the page

.user-select(sselect) {
  -webkit-user-select: sselect;
     -moz-user-select: sselect;
      -ms-user-select: sselect; // IE10+
          user-select: sselect;
}
